.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background: white;
  padding: 10px 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 90%; 
  max-height: 90vh; 
  position: relative;
  z-index: 101;
  overflow: hidden; 
  display: flex;
  flex-direction: column;
}

.modal-content {
  overflow-y: auto;
  flex-grow: 1;
  &:-webkit-scrollbar {
    display: none;  /* Safari and Chrome browsers */
  }

  & {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none;  /* mozilla */
  }
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  z-index: 102; 
}

@media (max-width: 480px) {
  .modal{
    width: 70%;
  }
}